import React from 'react'
import {Button,Form,Navbar,Nav,FormControl} from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import {BrowserRouter as Router, Switch,Route} from "react-router-dom";
import Pagenofount from './Pagenotfount';
import {Home} from './Home.jsx';
import {Login} from './Login';


const Menu =()=>{
    return (
<Router> 
    <div className="posicion">
 <Navbar bg="white" varian="">
<Navbar.Brand href="/">inicio</Navbar.Brand>
<Nav className="mr-auto">  
   <Nav.Link href="/Login/">logueo</Nav.Link> 
</Nav> 
<Form inline>
    <FormControl type="text" placeholder="buscar" className="mr-sm-02"/>
    <Button variant="outline-info">buscar</Button>
</Form>
</Navbar> 
</div>
<Switch>
    <Route path="/" exact component={Home}/>
    <Route path="/Login" exact component={Login}/>
    <Route component={Pagenofount}/>  
</Switch>
</Router>
)

};

export default Menu;